<template>
  <div>
    <b-row>
      <b-col
        md="2"
        class="p-0 m-0"
      >
        <side-bar-teams-units
          ref="refSideBarTeamsUnits"
          :form="form"
          :clean-filter="cleanFilter"
          :on-teams-change="onTeamsChange"
          :on-units-change="onUnitsChange"
          :on-option-change="onOptionChange"
          :on-search="getTrackingsByUnitsIds"
          @handle-change-dates="handleChangeDates"
        />
      </b-col>
      <b-col
        md="10"
        class="p-0 m-0"
      >
        <mapa-tracking
          ref="mapa"
          style="height: 800px;"
          :trackings="trackings"
          :init-map="initMap"
        />
      </b-col>
    </b-row>
    <side-bar-details
      :show-sidebar="showSidebar"
      :details="details"
    />
  </div>
</template>

<script>
import 'animate.css'
import { ref } from '@vue/composition-api'
import { useResponsiveAppLeftSidebarVisibility } from '@core/comp-functions/ui/app'
import {
  BCol,
  BRow,
} from 'bootstrap-vue'
import MapaTracking from './components/MapaTracking.vue'
import SideBarTeamsUnits from './components/SideBarTeamsUnits.vue'
import SideBarDetails from './components/SideBarDetails.vue'
import UseBinnacleTracking from './composables/UseBinnacleTracking'

export default {
  components: {
    BCol,
    BRow,
    MapaTracking,
    SideBarTeamsUnits,
    SideBarDetails,
  },
  created() {
    this.$store.commit('verticalMenu/UPDATE_VERTICAL_MENU_COLLAPSED', true)
  },
  destroyed() {
    this.$store.commit('verticalMenu/UPDATE_VERTICAL_MENU_COLLAPSED', false)
  },
  setup(__, ctx) {
    const { mqShallShowLeftSidebar } = useResponsiveAppLeftSidebarVisibility()
    const searchQuery = ref()
    const perfectScrollbarSettings = {
      maxScrollbarLength: 150,
    }
    const mapa = ref()
    const refSideBarTeamsUnits = ref()

    const {
      isConnected,
      showSidebar,
      details,
      form,
      trackings,
      cleanFilter,
      onTeamsChange,
      onUnitsChange,
      onOptionChange,
      handleChangeDates,
      getTrackingsByUnitsIds,
      initMap,
    } = UseBinnacleTracking(ctx, mapa)

    return {
      isConnected,
      searchQuery,
      perfectScrollbarSettings,
      mqShallShowLeftSidebar,
      form,
      showSidebar,
      details,
      trackings,
      mapa,
      refSideBarTeamsUnits,
      cleanFilter,
      onTeamsChange,
      onUnitsChange,
      onOptionChange,
      handleChangeDates,
      getTrackingsByUnitsIds,
      initMap,
    }
  },
}
</script>
