<template>
  <div
    class="sidebar-left"
    style="width: 95%;"
  >
    <div class="sidebar">
      <div class="sidebar-content todo-sidebar">
        <div class="todo-app-menu">
          <div class="add-task mb-2">
            <b-row class="mb-0">
              <b-col cols="12">
                <flat-pickr
                  v-model="dates"
                  class="form-control"
                  placeholder="Bucar por rango de fechas"
                  :config="{ mode: 'range', enableTime: true, dateFormat: 'Y-m-d H:i' }"
                  input-id="rangoFechas"
                  @on-close="(selectedDates, dateStr) =>
                    $emit('handle-change-dates', selectedDates, dateStr)
                  "
                />
              </b-col>
            </b-row>
            <b-row class="mt-1">
              <b-col cols="2">
                <b-button
                  v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                  variant="danger"
                  class="btn-icon"
                  @click="cleanFilter"
                >
                  <feather-icon icon="Trash2Icon" />
                </b-button>
              </b-col>
              <b-col cols="10">
                <b-button
                  v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                  variant="primary"
                  block
                  @click="onSearch"
                >
                  Buscar
                </b-button>
              </b-col>
            </b-row>
          </div>
          <vue-perfect-scrollbar
            :settings="perfectScrollbarSettings"
            class="sidebar-menu-list scroll-area"
          >
            <!-- Filters -->
            <!-- Teams -->
            <div class="mt-3 px-0 d-flex justify-content-start">
              <b-form-radio
                v-model="filterBy"
                value="teams"
                name="flavour-4a"
                class="custom-control-warning"
                inline
                @change="onOptionChange"
              >
                <h6 class="section-label mb-1">
                  Grupos
                </h6>
              </b-form-radio>
              <feather-icon
                class="ml-1"
                icon="TargetIcon"
              />
            </div>
            <app-collapse
              v-if="filterBy=='teams'"
              accordion
              type="default"
            >

              <custom-app-collapse-item
                v-for="team in teamsList"
                :key="team.id"
                :title="team.name"
              >
                <template v-slot:header>
                  <b-form-checkbox
                    v-model="form.teamsSelected"
                    :value="team.id"
                    inline
                    class="mt-1"
                    @change="()=>{
                      onTeamsChange(team)
                    }"
                  >
                    {{ team.name }}
                  </b-form-checkbox>
                </template>
                <div class="business-items">
                  <div
                    v-for="unit in team.units"
                    :key="unit.id"
                    class="business-item"
                  >
                    <div class="d-flex align-items-center justify-content-between">
                      <b-media
                        no-body
                      >
                        <b-media-aside class="mr-1">
                          <b-avatar
                            rounded
                            variant="light-primary"
                            size="34"
                          >
                            <feather-icon
                              icon="TruckIcon"
                              size="18"
                            />
                          </b-avatar>
                        </b-media-aside>
                        <b-media-body class="mt-05 font-weight-bolder">
                          {{ unit.plate }}
                        </b-media-body>
                      </b-media>
                    </div>
                  </div>
                </div>
              </custom-app-collapse-item>
            </app-collapse>
            <!-- Units -->
            <div class="mt-3 px-0 d-flex justify-content-start">
              <b-form-radio
                v-model="filterBy"
                value="units"
                name="flavour-4a"
                class="custom-control-warning"
                inline
              >
                <h6 class="section-label mb-1">
                  Unidades
                </h6>
              </b-form-radio>
              <feather-icon
                class="ml-1"
                icon="TruckIcon"
              />
            </div>
            <div v-if="filterBy=='units'">
              <div class="business-items">
                <div
                  v-for="unit in unitsList"
                  :key="unit.id"
                  class="business-item"
                >
                  <div class="d-flex align-items-center justify-content-between">
                    <b-form-checkbox
                      v-model="form.unitsSelected"
                      :value="unit.id"
                      class="mt-0"
                      inline
                      name="flavour-4a"
                      @change="onUnitsChange(unit.id)"
                    >
                      {{ unit.plate }}
                    </b-form-checkbox>
                  </div>
                </div>
              </div>
            </div>
          </vue-perfect-scrollbar>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppCollapse from '@core/components/app-collapse/AppCollapse.vue'
import CustomAppCollapseItem from '@core/components/app-collapse/CustomAppCollapseItem.vue'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import flatPickr from 'vue-flatpickr-component'
import {
  BAvatar,
  BButton,
  BCol,
  BFormCheckbox,
  BFormRadio,
  BMedia,
  BMediaAside,
  BMediaBody,
  BRow,
} from 'bootstrap-vue'
import { isDynamicRouteActive } from '@core/utils/utils'
import Ripple from 'vue-ripple-directive'
import useCatalogs from '@/composables/useCatalogs'
import { onMounted, ref } from '@vue/composition-api'
import useCustomFunctions from '@/composables/useCustomFunctions'

export default {
  directives: {
    Ripple,
  },
  components: {
    AppCollapse,
    CustomAppCollapseItem,
    BAvatar,
    BButton,
    BCol,
    BFormCheckbox,
    BFormRadio,
    BMedia,
    BMediaAside,
    BMediaBody,
    BRow,
    flatPickr,
    VuePerfectScrollbar,
  },
  props: {
    form: {
      required: true,
      type: Object,
    },
    cleanFilter: {
      required: true,
      type: Function,
    },
    onTeamsChange: {
      required: true,
      type: Function,
    },
    onUnitsChange: {
      required: true,
      type: Function,
    },
    onOptionChange: {
      required: true,
      type: Function,
    },
    onSearch: {
      required: true,
      type: Function,
    },
  },
  setup(__, ctx) {
    const STATUS_ACTIVE = ref(1)
    const STATUS_INACTIVE = ref(2)

    const perfectScrollbarSettings = {
      maxScrollbarLength: 60,
    }

    const {
      getUnitsWithDriverDevices,
      unitsList,
      getTeamsWithUnits,
      teamsList,
    } = useCatalogs()

    const {
      dateObjectoToString,
    } = useCustomFunctions()

    const filterBy = ref(false)
    const teamsSelected = ref([])
    const today = new Date()
    today.setHours(0, 0, 0, 0)
    const datesToday = [today, new Date()]
    const dates = ref(datesToday)
    const dateStr = dates.value.map(date => dateObjectoToString(date)).join(' a ')

    onMounted(() => {
      ctx.emit('handle-change-dates', datesToday, dateStr)
    })
    getUnitsWithDriverDevices()
    getTeamsWithUnits()

    return {
      STATUS_ACTIVE,
      STATUS_INACTIVE,
      perfectScrollbarSettings,
      isDynamicRouteActive,
      unitsList,
      teamsList,
      filterBy,
      teamsSelected,
      dates,
    }
  },
}
</script>
<style lang="scss" scoped>
@import "@core/scss/vue/libs/vue-flatpicker.scss";
.mt-05{
  margin-top: 6px !important;
}

</style>
