import Api from '@/services/Api'

const resource = 'v1/driver'
const resourceFile = 'v1/files/driver'
export default {
  fetchList({
    take,
    page,
    order,
    search,
  }) {
    return Api.get(`${resource}`, {
      params: {
        take,
        page,
        order,
        search,
      },
    })
  },
  create(form) {
    return Api.post(`${resource}`, form)
  },
  getById(id) {
    return Api.get(`${resource}/${id}`)
  },
  update(id, form) {
    return Api.patch(`${resource}/${id}`, form)
  },
  uploadPhoto(form) {
    const config = { headers: { 'Content-Type': 'multipart/form-data' } }
    return Api.post(`${resourceFile}`, form, config)
  },
  fetchAllActivesWhithouthUnit() {
    return Api.get(`${resource}/all/activesWhithouthUnit`)
  },
  fetchAllActivesWhithouthUnitExceptDriver(idDriver) {
    return Api.get(`${resource}/all/activesWhithouthUnitExcept/${idDriver}`)
  },
}
