<template>
  <b-sidebar
    bg-variant="white"
    right
    shadow
    backdrop
    :visible="showSidebar.show"
    @change="(val) => showSidebar.show = val"
  >
    <div>
      <div class="text-center">
        <!-- Avatar -->
        <b-avatar
          size="72px"
          class="mr-1"
          :src="details.driver.photo"
        />
        <!-- Name -->
        <b-card-text class="mt-2 h4 color-inherit text-reset">
          {{ details.driver.name }} {{ details.driver.lastName }}
        </b-card-text>
      </div>

      <b-list-group class="mt-1 rounded-0">
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="CreditCardIcon"
          />
          <span class="ml-1">
            Licencia: {{ details.driver.license }}
          </span>
        </b-list-group-item>
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="SmartphoneIcon"
          />
          <span class="ml-1">
            Celular: {{ details.driver.cellphone }}
          </span>
        </b-list-group-item>
      </b-list-group>
      <card-unit-details
        class="mt-0"
        :plate="details.plate"
        :brand="details.brand.name"
        :model="details.model.name"
      />
      <b-list-group class="mt-1 rounded-0">
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="MapPinIcon"
          />
          <span class="ml-0">
            Dirección: {{ details.lastTracking.address }}
          </span>
        </b-list-group-item>
        <b-list-group-item
          class="bg-transparent"
        >
          <feather-icon
            class="align-text-bottom"
            icon="CalendarIcon"
          />
          <span class="ml-0">
            Fecha: {{ dateTimeFormat(details.lastTracking.createdAt) }}
          </span>
        </b-list-group-item>
      </b-list-group>
      <card-device-details
        v-for="device in details.devices"
        :key="device.id"
        class="mt-0"
        :name="device.name"
        :type="device.typeDevice.name"
      />
    </div>
  </b-sidebar>
</template>

<script>
import {
  BSidebar,
  BAvatar,
  BCardText,
  BListGroup,
  BListGroupItem,
} from 'bootstrap-vue'
import useCustomFunctions from '@/composables/useCustomFunctions'
import CardUnitDetails from '../../alerts/components/CardUnitDetails.vue'
import CardDeviceDetails from '../../alerts/components/CardDeviceDetails.vue'

export default {
  components: {
    BSidebar,
    BAvatar,
    BCardText,
    BListGroup,
    BListGroupItem,
    CardUnitDetails,
    CardDeviceDetails,
  },
  props: {
    showSidebar: {
      required: true,
      type: Object,
    },
    details: {
      required: true,
      type: Object,
    },
  },
  setup() {
    const {
      dateTimeFormat,
    } = useCustomFunctions()
    return {
      dateTimeFormat,
    }
  },
}

</script>
