import Api from '@/services/Api'

const resource = 'v1/team'

export default {
  fetchList({
    take,
    page,
    order,
    search,
  }) {
    return Api.get(`${resource}`, {
      params: {
        take,
        page,
        order,
        search,
      },
    })
  },
  create(form) {
    return Api.post(`${resource}`, form)
  },
  getById(id) {
    return Api.get(`${resource}/${id}`)
  },
  getByIdWithUnits(id) {
    return Api.get(`${resource}/${id}/units`)
  },
  update(id, form) {
    return Api.patch(`${resource}/${id}`, form)
  },
  fetchAllActives() {
    return Api.get(`${resource}/all/actives`)
  },
  fetchAllActivesWithUnits() {
    return Api.get(`${resource}/all/activesWithUnits`)
  },
  addUnit(form) {
    return Api.post(`${resource}/add/unit`, form)
  },
  removeUnit(form) {
    return Api.post(`${resource}/remove/unit`, form)
  },
}
