import L from 'leaflet'

import { reactive, ref } from '@vue/composition-api'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import { useToast } from 'vue-toastification/composition'

import UnitService from '@/services/UnitService'
import { truckGreen, truckRed } from '@/views/trackings/icons'

const UseBinnacleTracking = (ctx, refMapa) => {
  const toast = useToast()
  const isLoading = ref(false)
  const showSidebar = reactive({
    show: false,
  })
  const units = ref([])
  const trackings = ref([])
  const unitsToFilter = ref([])
  const layer = ref()

  const form = reactive({
    unitsSelected: [],
    teamsSelected: [],
  })
  const markersList = ref([])
  const polylinesList = ref([])
  const detailsEmpty = {
    plate: '',
    brand: {
      name: '',
    },
    model: {
      name: '',
    },
    driver: {
      photo: '',
      name: '',
      lastName: '',
      cellphone: '',
    },
    devices: [],
    lastTracking: {
      address: '',
    },
  }
  const details = reactive(detailsEmpty)

  const queryParams = reactive({
    start: '',
    end: '',
    unitsIds: [],
  })

  const cleanFilter = () => {
    form.unitsSelected = []
    form.teamsSelected = []
    markersList.value = []
    unitsToFilter.value = []
    layer.value.clearLayers()
  }

  const alertValidation = () => {
    ctx.root.$swal({
      title: 'Información incompleta',
      text: ' Debes seleccionar fechas y almenos una unidad!',
      icon: 'warning',
      customClass: {
        confirmButton: 'btn btn-primary',
      },
      showClass: {
        popup: 'animate__animated animate__tada',
      },
      buttonsStyling: false,
    })
  }

  const drawPolylines = polylines => {
    if (!polylines.length) return
    // Crea y añade cada polilínea al mapa
    polylines.forEach(polyline => {
      const p = L.polyline(polyline.coordinates, { color: polyline.color, unitId: polyline.unitId }).addTo(layer.value)
      polylinesList.value.push(p)
    })
    // Ajusta el mapa para mostrar todas las polilíneas
    const bounds = L.latLngBounds(polylines.flatMap(polyline => polyline.coordinates))
    refMapa.value.$refs.mapLeaflet.fitBounds(bounds)
  }

  const drawStartMarker = (unitId, tracking, start = true) => {
    const { coordinates } = tracking.location
    const startIcon = L.divIcon({
      className: 'leaflet-data-marker',
      html: start ? truckGreen.replace('#', '%23') : truckRed.replace('#', '%23'),
      iconAnchor: [22, 28],
      iconSize: [36, 42],
      popupAnchor: [0, -30],
    })
    const coords = L.latLng(coordinates[1], coordinates[0])
    const marker = L.marker(coords, { icon: startIcon, title: 'Inico', unitId }).addTo(layer.value)
    markersList.value.push(marker)
  }

  const removePolylineAndMarkers = unitId => {
    const markersToDelete = markersList.value.filter(m => m.options.unitId === unitId)
    markersToDelete.forEach(marker => layer.value.removeLayer(marker))
    markersList.value = markersList.value.filter(m => m.options.unitId !== unitId)

    const polylinesToDelete = polylinesList.value.filter(p => p.options.unitId === unitId)
    polylinesToDelete.forEach(polyline => layer.value.removeLayer(polyline))
    polylinesList.value = polylinesList.value.filter(p => p.options.unitId !== unitId)
  }

  const trackingsToPolyline = unit => {
    const colors = ['blue', 'red', 'green', 'yellow', 'purple', 'orange', 'black', 'gray', 'cyan', 'magenta', 'brown']
    const coordinates = unit.trackings.map(t => [t.location.coordinates[1], t.location.coordinates[0]])
    if (unit.trackings.length > 1) {
      drawStartMarker(unit.id, unit.trackings[0])
      drawStartMarker(unit.id, unit.trackings[unit.trackings.length - 1], false)
    }
    return {
      unitId: unit.id,
      color: colors[Math.floor(Math.random() * colors.length)],
      coordinates,
    }
  }

  const getTrackingsByUnitsIds = async () => {
    layer.value.clearLayers()
    queryParams.unitsIds = unitsToFilter.value
    if (!queryParams.unitsIds.length || !queryParams.start || !queryParams.end) {
      alertValidation()
      return
    }
    try {
      const response = await UnitService.findTrackingsByUnitId(queryParams)
      const polylines = response.data.map(trackingsToPolyline)
      drawPolylines(polylines)
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al consultar trackings',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const handleChangeDates = (selectedDates, dateStr) => {
    if (dateStr === '') {
      queryParams.start = ''
      queryParams.end = ''
      return
    }

    const arrayDates = dateStr.split('a')
    if (arrayDates.length === 1) {
      const [uniqueDate] = arrayDates
      const withoutSpaces = uniqueDate.trimEnd()
      queryParams.start = withoutSpaces
      queryParams.end = withoutSpaces
    } else {
      const [start, end] = arrayDates
      queryParams.start = start.trimEnd()
      queryParams.end = end.trimStart()
    }
  }

  const onTeamsChange = team => {
    const isSelected = form.teamsSelected.includes(team.id)
    const unitsOfTeam = team.units.map(unit => unit.id)
    if (isSelected) {
      form.unitsSelected = [...form.unitsSelected, ...unitsOfTeam]
      unitsToFilter.value = [...unitsToFilter.value, ...unitsOfTeam]
    } else {
      form.unitsSelected = form.unitsSelected.filter(unitId => !unitsOfTeam.includes(unitId))
      unitsToFilter.value = unitsToFilter.value.filter(unitId => !unitsOfTeam.includes(unitId))
      unitsOfTeam.map(removePolylineAndMarkers)
    }
  }
  const onUnitsChange = unitId => {
    const selected = form.unitsSelected.includes(unitId)
    if (selected) {
      unitsToFilter.value.push(unitId)
    } else {
      unitsToFilter.value = unitsToFilter.value.filter(u => u !== unitId)
      removePolylineAndMarkers(unitId)
    }
  }

  const onOptionChange = value => {
    if (value === 'teams') {
      cleanFilter()
    }
  }

  const initMap = () => {
    layer.value = L.layerGroup().addTo(refMapa.value.$refs.mapLeaflet.mapObject)
  }

  return {
    isLoading,
    units,
    trackings,
    form,
    showSidebar,
    details,
    cleanFilter,
    onTeamsChange,
    onUnitsChange,
    onOptionChange,
    initMap,
    handleChangeDates,
    getTrackingsByUnitsIds,
  }
}

export default UseBinnacleTracking
