import Api from '@/services/Api'

const resource = 'v1/unit'

export default {
  fetchList({
    take,
    page,
    order,
    search,
  }) {
    return Api.get(`${resource}`, {
      params: {
        take,
        page,
        order,
        search,
      },
    })
  },
  create(form) {
    return Api.post(`${resource}`, form)
  },
  getById(id) {
    return Api.get(`${resource}/${id}`)
  },
  update(id, form) {
    return Api.patch(`${resource}/${id}`, form)
  },
  getWithLastTrackingById(unitId, trackingId) {
    return Api.get(`${resource}/withLastTracking/${unitId}/${trackingId}`)
  },
  fetchAllActivesWithDriverDevices() {
    return Api.get(`${resource}/all/activesWhithDriverDevices`)
  },
  fetchAllActives() {
    return Api.get(`${resource}/all/actives`)
  },
  fetchAllActivesReadyToUse() {
    return Api.get(`${resource}/all/activesReadyToUse`)
  },
  findAllWithoutTeam() {
    return Api.get(`${resource}/all/withoutTeam`)
  },
  findAllWithoutTeamExcept(id) {
    return Api.get(`${resource}/all/withoutTeamExcept/${id}`)
  },
  findTrackingsByUnitId(params) {
    return Api.get(`${resource}/all/trackingsByUnitsIdsAndDates`, { params })
  },
}
