import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import { ref } from '@vue/composition-api'

import BrandService from '@/services/BrandService'
import DeviceService from '@/services/DeviceService'
import DriverService from '@/services/DriverService'
import ModelService from '@/services/ModelService'
import TeamService from '@/services/TeamService'
import TypeDeviceService from '@/services/TypeDeviceService'
import UnitService from '@/services/UnitService'

const useCatalogs = () => {
  const toast = useToast()

  const brandsList = ref([])
  const devicesList = ref([])
  const driversList = ref([])
  const modelsList = ref([])
  const typeDevicesList = ref([])
  const teamsList = ref([])
  const unitsList = ref([])

  const getBrands = async () => {
    try {
      const response = await BrandService.fetchAllActives()
      brandsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Catálogo de marcas',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getDevices = async () => {
    try {
      const response = await DeviceService.fetchAllActivesWhithouthUnit()
      devicesList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Dispositivos',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getDevicesExcept = async idDevice => {
    try {
      const response = await DeviceService.fetchAllActivesWhithouthUnitExceptDevice(idDevice)
      devicesList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Dispositivos',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getDrivers = async () => {
    try {
      const response = await DriverService.fetchAllActivesWhithouthUnit()
      driversList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar   Conductores',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getDriversExcept = async idDriver => {
    try {
      const response = await DriverService.fetchAllActivesWhithouthUnitExceptDriver(idDriver)
      driversList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar   Conductores',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getModels = async () => {
    try {
      const response = await ModelService.fetchAllActives()
      modelsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar  Modelos',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getTeams = async () => {
    try {
      const response = await TeamService.fetchAllActives()
      teamsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Equipos',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getTeamsWithUnits = async () => {
    try {
      const response = await TeamService.fetchAllActivesWithUnits()
      teamsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Equipos',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getTypeDevices = async () => {
    try {
      const response = await TypeDeviceService.fetchAllActives()
      typeDevicesList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Catálogo de tipos de dispositivos',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getUnitsWithDriverDevices = async () => {
    try {
      const response = await UnitService.fetchAllActivesWithDriverDevices()
      unitsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Catálogo de Unidades',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getUnitsReadyToUse = async () => {
    try {
      const response = await UnitService.fetchAllActivesReadyToUse()
      unitsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Catálogo de Unidades',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getUnitsWithoutTeam = async () => {
    try {
      const response = await UnitService.findAllWithoutTeam()
      unitsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Catálogo de Unidades',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getUnitsWithoutTeamExcept = async idUnit => {
    try {
      const response = await UnitService.findAllWithoutTeamExcept(idUnit)
      unitsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Catálogo de Unidades',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const getUnits = async () => {
    try {
      const response = await UnitService.fetchAllActives()
      unitsList.value = response.data
    } catch (error) {
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al cargar Catálogo de Unidades',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  return {
    devicesList,
    getDevices,
    getDevicesExcept,

    brandsList,
    getBrands,

    driversList,
    getDrivers,
    getDriversExcept,

    modelsList,
    getModels,

    typeDevicesList,
    getTypeDevices,

    teamsList,
    getTeamsWithUnits,
    getTeams,

    unitsList,
    getUnits,
    getUnitsWithDriverDevices,
    getUnitsReadyToUse,
    getUnitsWithoutTeam,
    getUnitsWithoutTeamExcept,

  }
}
export default useCatalogs
